* {
    font-family: 'EB Garamond', serif !important;
}

body {
    margin: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    letter-spacing: 0.1rem;
}

main {
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;

    box-shadow: inset 0 0 8rem 4rem #fff2;
    color: #fff;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

button {
    padding: 1rem 4rem;
    border: none;
    background-color: #fff2;
    color: #fff;
    border-radius: 2rem;
    font-size: 1rem;
    letter-spacing: 0.1rem;
    font-weight: 700;
}

p {
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 1.5rem;
    margin: 0.5rem;
}

.little {
    font-size: 0.8rem;
    line-height: 1rem;
}

h2 {
    font-weight: 800;
    font-size: 1.5rem;
    letter-spacing: 0.4rem;
    text-transform: uppercase;
}

.backgroundCanvas {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background-color: #000;
}

.displayBox {
    text-align: center;
    padding: 2rem;
    animation: fade-in 4s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.plant {
    background-color: #fff2;
    color: #fff;
}

.pixelated {
    image-rendering: pixelated;
}

@keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateY(1rem);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.sceneTransition {
    animation: scene-transition 2s ease-in-out;
}

@keyframes scene-transition {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.activeInfo {
    background-color: #fff;
}